<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 采集编码 -->
            <a-form-model-item
              label="采集编码"
              prop="collectionCode"
              ref="collectionCode"
            >
              <a-input
                v-model="entity.collectionCode"
                placeholder="请输入采集编码"
                name="collectionCode"
                :disabled="!!ndoEditParams.row"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 采集名称 -->
            <a-form-model-item
              label="采集名称"
              prop="name"
              ref="name"
            >
              <a-input
                v-model="entity.name"
                placeholder="请输入采集名称"
                name="name"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 采集类型 -->
            <a-form-model-item
              label="采集类型"
              prop="collectionType"
              ref="collectionType"
            >
              <a-select
                v-model="entity.collectionType"
                placeholder="请选择采集类型"
                name="collectionType"
              >
                <a-select-option value="设备数据采集">设备数据采集</a-select-option>
                <a-select-option value="生产过程采集">生产过程采集</a-select-option>
                <a-select-option value="质量检测采集">质量检测采集</a-select-option>
                <a-select-option value="工艺参数采集">工艺参数采集</a-select-option>
                <a-select-option value="环境数据采集">环境数据采集</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 设备类型 -->
            <a-form-model-item
              label="设备类型"
              prop="deviceType"
              ref="deviceType"
            >
              <a-select
                v-model="entity.deviceType"
                placeholder="请选择设备类型"
                name="deviceType"
              >
                <a-select-option value="传感器">传感器</a-select-option>
                <a-select-option value="PLC">PLC</a-select-option>
                <a-select-option value="仪表">仪表</a-select-option>
                <a-select-option value="摄像头">摄像头</a-select-option>
                <a-select-option value="控制器">控制器</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 协议 -->
            <a-form-model-item
              label="通信协议"
              prop="protocol"
              ref="protocol"
            >
              <a-select
                v-model="entity.protocol"
                placeholder="请选择通信协议"
                name="protocol"
                @change="handleProtocolChange"
              >
                <a-select-option value="Modbus">Modbus</a-select-option>
                <a-select-option value="OPC UA">OPC UA</a-select-option>
                <a-select-option value="MQTT">MQTT</a-select-option>
                <a-select-option value="HTTP">HTTP</a-select-option>
                <a-select-option value="TCP/IP">TCP/IP</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 状态 -->
            <a-form-model-item
              label="状态"
              prop="status"
              ref="status"
            >
              <a-select
                v-model="entity.status"
                placeholder="请选择状态"
                name="status"
              >
                <a-select-option value="已启用">已启用</a-select-option>
                <a-select-option value="已禁用">已禁用</a-select-option>
                <a-select-option value="配置中">配置中</a-select-option>
                <a-select-option value="异常">异常</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 采集频率 -->
            <a-form-model-item
              label="采集频率(秒/次)"
              prop="frequencyValue"
              ref="frequencyValue"
            >
              <a-select
                v-model="entity.frequencyValue"
                placeholder="请选择采集频率"
                name="frequencyValue"
              >
                <a-select-option :value="1">1秒/次</a-select-option>
                <a-select-option :value="5">5秒/次</a-select-option>
                <a-select-option :value="10">10秒/次</a-select-option>
                <a-select-option :value="30">30秒/次</a-select-option>
                <a-select-option :value="60">60秒/次</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 上次采集时间 -->
            <a-form-model-item
              label="上次采集时间"
            >
              <span>{{ entity.lastCollectTime || '无' }}</span>
            </a-form-model-item>
          </a-col>
          <a-col :sm="12">
            <!-- 描述 -->
            <a-form-model-item 
              label="描述" 
              prop="description"
              ref="description"
            >
              <a-textarea
                v-model="entity.description"
                placeholder="请输入描述信息"
                :rows="3"
                :max-length="500"
                name="description"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>

      <!-- 连接设置 -->
      <g-collapse headerName="连接设置">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <a-form-model-item
                label="IP地址"
                prop="ipAddress"
                ref="ipAddress"
              >
                <a-input
                  v-model="entity.ipAddress"
                  placeholder="请输入IP地址"
                  name="ipAddress"
                />
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item
                label="端口"
                prop="port"
                ref="port"
              >
                <a-input-number
                  v-model="entity.port"
                  placeholder="请输入端口"
                  :min="1"
                  :max="65535"
                  style="width: 100%"
                />
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item
                label="超时时间(秒)"
                prop="timeout"
                ref="timeout"
              >
                <a-input-number
                  v-model="entity.timeout"
                  placeholder="请输入超时时间"
                  :min="1"
                  :max="300"
                  style="width: 100%"
                />
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item
                label="重试次数"
                prop="retryCount"
                ref="retryCount"
              >
                <a-input-number
                  v-model="entity.retryCount"
                  placeholder="请输入重试次数"
                  :min="0"
                  :max="10"
                  style="width: 100%"
                />
              </a-form-model-item>
            </a-col>
            <template v-if="entity.protocol === 'OPC UA' || entity.protocol === 'MQTT'">
              <a-col :sm="6">
                <a-form-model-item
                  label="用户名"
                  prop="username"
                  ref="username"
                >
                  <a-input
                    v-model="entity.username"
                    placeholder="请输入用户名"
                    name="username"
                  />
                </a-form-model-item>
              </a-col>
              <a-col :sm="6">
                <a-form-model-item
                  label="密码"
                  prop="password"
                  ref="password"
                >
                  <a-input-password
                    v-model="entity.password"
                    placeholder="请输入密码"
                    name="password"
                  />
                </a-form-model-item>
              </a-col>
            </template>
            <template v-if="entity.protocol === 'Modbus'">
              <a-col :sm="6">
                <a-form-model-item
                  label="从站ID"
                  prop="slaveId"
                  ref="slaveId"
                >
                  <a-input-number
                    v-model="entity.slaveId"
                    placeholder="请输入从站ID"
                    :min="1"
                    :max="255"
                    style="width: 100%"
                  />
                </a-form-model-item>
              </a-col>
              <a-col :sm="6">
                <a-form-model-item
                  label="功能码"
                  prop="functionCode"
                  ref="functionCode"
                >
                  <a-select
                    v-model="entity.functionCode"
                    placeholder="请选择功能码"
                    name="functionCode"
                  >
                    <a-select-option value="1">01 - 读线圈</a-select-option>
                    <a-select-option value="2">02 - 读离散输入</a-select-option>
                    <a-select-option value="3">03 - 读保持寄存器</a-select-option>
                    <a-select-option value="4">04 - 读输入寄存器</a-select-option>
                  </a-select>
                </a-form-model-item>
              </a-col>
            </template>
          </a-row>
          <a-row v-if="entity.protocol === 'HTTP'">
            <a-col :sm="12">
              <a-form-model-item
                label="URL路径"
                prop="urlPath"
                ref="urlPath"
              >
                <a-input
                  v-model="entity.urlPath"
                  placeholder="请输入URL路径"
                  name="urlPath"
                />
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item
                label="请求方法"
                prop="httpMethod"
                ref="httpMethod"
              >
                <a-select
                  v-model="entity.httpMethod"
                  placeholder="请选择请求方法"
                  name="httpMethod"
                >
                  <a-select-option value="GET">GET</a-select-option>
                  <a-select-option value="POST">POST</a-select-option>
                  <a-select-option value="PUT">PUT</a-select-option>
                </a-select>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>

      <!-- 数据点配置 -->
      <g-collapse headerName="数据点配置">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="24">
              <a-button class="add-point-btn" type="primary" icon="plus" @click="addDataPoint">添加数据点</a-button>
            </a-col>
            <a-col :sm="24">
              <a-table 
                :columns="dataPointColumns" 
                :dataSource="entity.dataPoints" 
                :pagination="false"
                size="small"
                rowKey="id"
              >
                <template slot="pointName" slot-scope="text, record, index">
                  <a-input
                    v-model="entity.dataPoints[index].pointName"
                    placeholder="请输入数据点名称"
                  />
                </template>
                <template slot="address" slot-scope="text, record, index">
                  <a-input
                    v-model="entity.dataPoints[index].address"
                    placeholder="请输入地址/标签"
                  />
                </template>
                <template slot="dataType" slot-scope="text, record, index">
                  <a-select
                    v-model="entity.dataPoints[index].dataType"
                    placeholder="请选择数据类型"
                    style="width: 100%"
                  >
                    <a-select-option value="Int">Int</a-select-option>
                    <a-select-option value="Float">Float</a-select-option>
                    <a-select-option value="Boolean">Boolean</a-select-option>
                    <a-select-option value="String">String</a-select-option>
                  </a-select>
                </template>
                <template slot="unit" slot-scope="text, record, index">
                  <a-input
                    v-model="entity.dataPoints[index].unit"
                    placeholder="请输入单位"
                  />
                </template>
                <template slot="description" slot-scope="text, record, index">
                  <a-input
                    v-model="entity.dataPoints[index].description"
                    placeholder="请输入描述"
                  />
                </template>
                <template slot="action" slot-scope="text, record, index">
                  <a-button 
                    type="danger" 
                    icon="delete" 
                    size="small" 
                    @click="() => removeDataPoint(index)"
                  />
                </template>
              </a-table>
            </a-col>
          </a-row>
        </template>
      </g-collapse>

      <!-- 元数据信息 -->
      <g-collapse headerName="详细信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 创建时间 -->
              <a-form-model-item label="创建时间">
                <span>{{ entity.createdTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改人 -->
              <a-form-model-item label="修改人">
                <span>{{ entity.modifiedBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改时间 -->
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditDataCollection",
  mixins: [GNdoPageEditBase],
  
  data() {
    return {
      loading: false,
      entity: {
        id: undefined,
        collectionCode: "",
        name: "",
        collectionType: "",
        deviceType: "",
        protocol: "",
        frequencyValue: 5,
        frequency: "5秒/次",
        ipAddress: "",
        port: 502,
        timeout: 10,
        retryCount: 3,
        username: "",
        password: "",
        slaveId: 1,
        functionCode: "3",
        urlPath: "",
        httpMethod: "GET",
        dataPoints: [],
        description: "",
        lastCollectTime: "",
        status: "配置中",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: ""
      },
      rules: {
        collectionCode: [
          { required: true, message: "请输入采集编码", trigger: "change" },
          { pattern: /^DC\d{4}$/, message: "采集编码格式不正确，应为DC开头加4位数字", trigger: "change" }
        ],
        name: [
          { required: true, message: "请输入采集名称", trigger: "change" },
          { pattern: /^[^\s]+(\s+[^\s]+)*$/, message: "采集名称不能包含前后空格", trigger: "change" }
        ],
        collectionType: [
          { required: true, message: "请选择采集类型", trigger: "change" }
        ],
        deviceType: [
          { required: true, message: "请选择设备类型", trigger: "change" }
        ],
        protocol: [
          { required: true, message: "请选择通信协议", trigger: "change" }
        ],
        frequencyValue: [
          { required: true, message: "请选择采集频率", trigger: "change" }
        ],
        ipAddress: [
          { required: true, message: "请输入IP地址", trigger: "change" },
          { pattern: /^(\d{1,3}\.){3}\d{1,3}$/, message: "IP地址格式不正确", trigger: "change" }
        ],
        port: [
          { required: true, message: "请输入端口", trigger: "change" },
          { type: "number", min: 1, max: 65535, message: "端口范围为1-65535", trigger: "change" }
        ],
        timeout: [
          { required: true, message: "请输入超时时间", trigger: "change" },
          { type: "number", min: 1, max: 300, message: "超时时间范围为1-300秒", trigger: "change" }
        ],
        slaveId: [
          { required: true, message: "请输入从站ID", trigger: "change" },
          { type: "number", min: 1, max: 255, message: "从站ID范围为1-255", trigger: "change" }
        ],
        functionCode: [
          { required: true, message: "请选择功能码", trigger: "change" }
        ],
        urlPath: [
          { required: true, message: "请输入URL路径", trigger: "change" }
        ],
        httpMethod: [
          { required: true, message: "请选择请求方法", trigger: "change" }
        ]
      },
      dataPointColumns: [
        {
          title: "数据点名称",
          dataIndex: "pointName",
          key: "pointName",
          width: "20%",
          scopedSlots: { customRender: "pointName" }
        },
        {
          title: "地址/标签",
          dataIndex: "address",
          key: "address",
          width: "15%",
          scopedSlots: { customRender: "address" }
        },
        {
          title: "数据类型",
          dataIndex: "dataType",
          key: "dataType",
          width: "15%",
          scopedSlots: { customRender: "dataType" }
        },
        {
          title: "单位",
          dataIndex: "unit",
          key: "unit",
          width: "10%",
          scopedSlots: { customRender: "unit" }
        },
        {
          title: "描述",
          dataIndex: "description",
          key: "description",
          width: "30%",
          scopedSlots: { customRender: "description" }
        },
        {
          title: "操作",
          key: "action",
          width: "10%",
          scopedSlots: { customRender: "action" }
        }
      ]
    };
  },
  
  methods: {
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取数据采集配置
        setTimeout(() => {
          this.initFormData();
          this.loading = false;
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },
    
    resetFormData() {
      this.entity = {
        id: undefined,
        collectionCode: "",
        name: "",
        collectionType: "",
        deviceType: "",
        protocol: "",
        frequencyValue: 5,
        frequency: "5秒/次",
        ipAddress: "",
        port: 502,
        timeout: 10,
        retryCount: 3,
        username: "",
        password: "",
        slaveId: 1,
        functionCode: "3",
        urlPath: "",
        httpMethod: "GET",
        dataPoints: [],
        description: "",
        lastCollectTime: "",
        status: "配置中",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: ""
      };
    },
    
    initFormData() {
      if (this.ndoEditParams.row) {
        const row = this.ndoEditParams.row;
        
        // 从frequency中提取数值部分
        let frequencyValue = 5;
        if (row.frequency) {
          const match = row.frequency.match(/(\d+)/);
          if (match) {
            frequencyValue = parseInt(match[1]);
          }
        }
        
        // 初始化dataPoints数组
        let dataPoints = [];
        if (row.dataPoints) {
          // 如果是数值，则生成默认数据点
          if (typeof row.dataPoints === 'number') {
            for (let i = 0; i < row.dataPoints; i++) {
              dataPoints.push({
                id: Date.now() + i,
                pointName: `数据点${i+1}`,
                address: `addr${i+1}`,
                dataType: i % 2 ? 'Int' : 'Float',
                unit: i % 3 ? '℃' : (i % 4 ? 'MPa' : '%'),
                description: `数据点${i+1}描述`
              });
            }
          } else if (Array.isArray(row.dataPoints)) {
            dataPoints = row.dataPoints;
          }
        }
        
        this.entity = {
          ...this.entity,
          ...row,
          frequencyValue,
          dataPoints,
        };
      }
    },
    
    async save() {
      // 保存前更新frequency字段
      this.entity.frequency = `${this.entity.frequencyValue}秒/次`;
      
      // 保存校验
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        // 模拟保存操作
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.name);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },
    
    inChange(e) {
      console.log(e.target.value);
    },
    
    // 添加数据点
    addDataPoint() {
      this.entity.dataPoints.push({
        id: Date.now(),
        pointName: "",
        address: "",
        dataType: "Float",
        unit: "",
        description: ""
      });
    },
    
    // 删除数据点
    removeDataPoint(index) {
      this.entity.dataPoints.splice(index, 1);
    },
    
    // 处理协议变更
    handleProtocolChange(value) {
      // 根据协议类型设置默认端口
      switch(value) {
        case 'Modbus':
          this.entity.port = 502;
          break;
        case 'OPC UA':
          this.entity.port = 4840;
          break;
        case 'MQTT':
          this.entity.port = 1883;
          break;
        case 'HTTP':
          this.entity.port = 80;
          break;
        case 'TCP/IP':
          this.entity.port = 9600;
          break;
        default:
          this.entity.port = 502;
      }
    }
  },
};
</script>

<style lang="scss" scoped>
.card {
  margin-bottom: 16px;
  padding: 16px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}

.add-point-btn {
  margin-bottom: 16px;
}
</style> 